@use "../../styles/index.scss" as *;

.slider-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: 10px 0 60px 0;
}
.slider {
  position: relative;
  display: block;
}
.slide {
  position: relative;
  display: block;
  transition: transform 0.7s ease-in-out;
}
.arrow-button{
  position: absolute;
  top: 50%;
  transform: translateY(-75%);
  background-color: transparent;
  font-size: 3rem;
  border: none;
  padding: 10px;
  cursor: pointer;
  
  svg {
    color: $white;
    border: 0;
    border-radius: 50%;
    background-color: $blue;
    box-shadow: 0px 0px 20px 0px rgba(66, 68, 90, 1);
    transition: .3s ease-in-out;

    &:hover {
      color: $yellow;
      background-color: $white;
    }
  }
  
}
.prev-button {
  left: 0;
}
.next-button {
  right: 0;
}

.product-card {
  display: block;
  float: left;
  box-sizing: border-box;
  list-style-type: none;
  text-align: center;
  border-radius: 5px;
}